








var _self;
var graceRichText = require("../../graceUI/jsTools/richText.js");
export default {
	data() {
		return {
			demoHtml : ''
		};
	},
	onLoad:function(){
		_self = this;
		// 加载 html 内容
		uni.request({
			url:'http://grace.hcoder.net/richTextDemo.php',
			success:function(res){
				// 强化 richText
				_self.demoHtml = graceRichText.format(res.data);
			}
		});
	},
	methods:{
		
	}
}
